@import "../styles.scss";

.users-options-content {
  display: flex;
  gap: 1rem;
  padding: 1rem;
  position: absolute;
  right: 2vw;
  bottom: 30vh;
  z-index: 4;
  opacity: 1;
  visibility: visible;
}

.open-menu {
  height: 60px;
  background-color: $dark-green;
  width: 60px;
  display: flex;
  justify-content: center;
  border-radius: 50%;
  padding: 1rem;
  z-index: 5;
  transition: transform 0.2s ease;
  &:active {
    transform: scale(0.9);
  }
  img {
    filter: $white-svg;
    height: 100%;
  }
}

.search-input {
  display: none;
  padding: $input-padding;
  border-radius: 1rem;
  outline: none;
  border: thin solid $blue-two;
  opacity: 0;
  visibility: hidden;
  &.visible {
    display: inline;
    opacity: 1;
    visibility: visible;
    animation: 0.2s fade-in-up-animation 0s ease-in-out alternate;
  }
  &::placeholder {
    font-size: 0.8rem;
    font-weight: bold;
    text-align: center;
  }
}

.btn-add {
  display: none;
  border: none;
  border-radius: $input-padding;
  outline: none;
  height: auto;
  color: $white;
  cursor: pointer;
  background-color: $dark-green;
  padding: $input-padding;
  transition: background 0.2s ease, transform 0.2s ease;
  opacity: 0;
  visibility: hidden;
  &.visible {
    display: inline;
    opacity: 1;
    visibility: visible;
    animation: 0.2s fade-in-up-animation 0s ease-in-out alternate;
  }
  span {
    display: none;
  }
  &:hover {
    background-color: $blue-one;
  }
  &:active {
    transform: scale(0.95);
  }

  & img {
    height: auto;
    width: 32px;
    vertical-align: middle;
    filter: $white-svg;
  }
}

@media (min-width: $mobile-query) {
  .open-menu {
    opacity: 0;
    visibility: hidden;
    display: none;
  }
  .users-options-content {
    position: static;
    display: flex;
    flex-direction: column;
    align-items: center;
    opacity: 1;
    visibility: visible;

    .search-input, .btn-add{
      display: inline;
      opacity: 1;
      visibility: visible;
    }
  }
  .btn-add-user {
    span {
      display: inline;
    }
  }
}
