@import "../styles.scss";

.form-select {
  padding: 0.4rem;
  width: 100%;
  text-align: center;
  font-size: 1rem;
  font-weight: bold;
  color: $blue-one;
  border: none;
  border-left: 3px solid $blue-two;
  border-right: 3px solid $blue-two;
  outline: none;
  background-color: $blue-four;
  margin-bottom: .5rem;
  &:focus {
    border-left: 3px solid $orange;
    border-right: 3px solid $orange;
  }
  &.form-error{
    border-left: 3px solid $light-red;
    border-right: 3px solid $light-red;
  }
}
