@import "../styles.scss";
.modal-message {
  display: block;
  padding: 1rem;
  width: 90%;
  max-width: 320px;
  border-radius: 10px;
  z-index: 500;
  position: absolute;
  // transform: translateY(-100%);
  top: 2vw;
  right: 2vw;

  h2 {
    font-size: 1.2rem;
  }
  p {
    font-size: 1rem;
  }

  &.modal-error {
    background-color: rgba(255, 161, 161, 0.364);
    color: $dark-red;
    animation: 3s modal-message 0s ease-in-out alternate;
  }
  &.modal-success {
    background-color: rgba(205, 249, 255, 0.844);
    color: #1069c9;
    animation: 3s modal-message 0s ease-in-out alternate;
  }
}

@keyframes modal-message {
  0% {
    opacity: 0;
    top: 1vh;
  }
  20% {
    top: 3vh;
    opacity: 1;
  }

  80% {
    opacity: 1;
    top: 2vh;
  }
  100% {
    opacity: 0;
    top: -100%;
  }
}
