@import "../../../../../styles.scss";

.user-grid-item {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: auto;
  -webkit-box-shadow: 0px 0px 10px 2px $dark-green;
  box-shadow: 3px 3px 2px -1px $blue-two;
  border-radius: 1rem;
  border: 1px solid #44a19f4b;
  overflow: hidden;
  background-color: $blue-four;
  animation: 0.2s fade-in-up-animation 0s ease-in-out alternate;

  .user-image {
    width: 100%;
    margin: auto;
    padding: 0.4rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    border-radius: 1rem;
    // pc versions

    img {
      height: 40px;
      width: 40px;
      object-fit: cover;
      border-radius: 50%;
      -webkit-box-shadow: -3px 3px 4px 2px $dark-green;
      box-shadow: -3px 3px 5px -1px $blue-two;
    }

    .user-username {
      font-size: 1rem;
      color: $dark-green;
      font-weight: bold;
      text-align: center;
    }
  }
  .user-rol {
    width: fit-content;
    margin: auto;
    font-size: 0.8rem;
    border-radius: 1rem;
    padding: 0.4rem 0.8rem;
    font-weight: bold;
    &.admin {
      color: $light-red;
    }
    &.user {
      color: $dark-green;
    }
  }
  .user-info {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: column;

    & > * {
      font-size: 0.8rem;
      color: $font-color;
    }
  }
}

.user-actions {
  padding: 0.2rem;
  display: flex;
  align-items: center;
  gap: 0.4rem;
  justify-content: center;
  button {
    outline: none;
    border: none;
    width: 100%;
    text-align: center;
    font-size: 0.8rem;
    text-decoration: none;
    color: $white;
    padding: 0.4rem 0.8rem;
    border-radius: 1rem;
    cursor: pointer;
    transition: transform 0.2s ease;
    & img {
      filter: $white-svg;
      height: 16px;
    }
    &:active {
      transform: scale(0.9);
    }
  }
  .user-edit {
    background-color: $yellow;
    &:hover {
      background-color: $light-yellow-2;
    }
  }
  .user-drop {
    background-color: $dark-red;
    &:hover {
      background-color: $light-red;
    }
  }
}

@media (min-width: 720px) {
  .user-grid-item {
    flex-direction: column;
    max-width: 16rem;
    .user-image{
      img{
        height: 60px;
        width: 60px;
      }
    }
  }
}

@keyframes user-card-animation {
  0% {
    opacity: 0.2;
    transform: translateY(10%);
  }
  100% {
    opacity: 1;
    transform: translateY(0%);
  }
}
