@import "../styles.scss";

// Form errors
.login-error {
  background-color: $light-red;
  padding: $input-padding;
  font-size: 0.8rem;
  border-radius: $input-padding;
  color: $white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  animation: .8s error-input 0s ease-in-out alternate;
  margin-bottom: 1rem;
}

.login-error svg {
  max-height: 1rem;
  vertical-align: baseline;
  min-width: 30px;
  filter: invert(92%) sepia(41%) saturate(117%) hue-rotate(2deg)
    brightness(108%) contrast(106%);
    
    
}

@keyframes error-input{
  0%{
    opacity: 0;
    transform: scale(.1);
  }
  80%{
    transform: scale(1.1);
  }
  100%{
    opacity: 1;
    transform: scale(1);
  }
}
