@import "../../../styles.scss";

.navbar {
  grid-area: navbar;
  bottom: 0;
  padding: 1rem;
  background-color: $blue-four;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  width: 100%;
  height: 60px;
  z-index: 10;
  animation: 1s navbar 0s ease-in-out alternate;

  ul {
    display: flex;
    justify-content: center;
    gap: 0.5rem;
    list-style: none;
    background-color: $blue-one;
    border-radius: 1rem;
    border: thin solid $blue-one;
  }

  .nav-link {
    border-radius: 1rem;
    max-width: 80px;
    width: 100%;
    padding: 1rem;
    text-decoration: none;
    transition: transform 0.2s ease, background 0.2s ease;

    span {
      display: none;
      z-index: 1;
    }

    &:hover,
    &.active {
      background-color: $white;
      img {
        transform: rotate(-10deg);
        filter: $dark-green-svg;
      }
    }

    img {
      height: 28px;
      width: 28px;
      display: block;
      margin: auto;
      transition: transform 0.3s ease;
      filter: $white-svg;
    }
  }
}

@media (min-width: 720px) {
  .navbar {
    height: 100%;
    width: 80px;
    padding: 0;
    ul {
      flex-direction: column;
      width: 100%;
    }
    .nav-link {
      position: relative;
      z-index: 10000;
      padding: 1.5rem 0.8rem;
      display: flex;
      img {
        width: 100%;
        transition: transform 0.3s ease;
      }

      &:hover {
        transform: scale(1.03);
        span {
          display: block;
          position: absolute;
          padding: 0.8rem 0.8rem;
          border-radius: 0rem 2rem 2rem 0rem;
          top: 25%;
          background-color: $dark-green;
          color: $white;
          transform: translate(4rem);
          animation: 0.2s navbar-span 0s ease-in-out alternate;
        }
      }
    }
  }
}

// NAVBAR ANIMATION
@keyframes navbar {
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }
  100% {
    opacity: 1;
    transform: translateX(0%);
  }
}
// SPAN ANIMATION
@keyframes navbar-span {
  0% {
    opacity: 0;
    transform: translateX(50%);
  }
  80% {
    opacity: .6;
  }
  100% {
    opacity: 1;
    transform: translateX(4rem);
  }
}
