$font-color: rgb(39, 39, 39);
$yellow: #cea02d;
$light-yellow: #feffcb;
$light-yellow-2: #ccae60;
$orange: #ffb576;
$light-red: #ff6b83;
$dark-red: #c6394d;

$white: #fffffa;
$dark-green: #0d5c63;
$blue-one: #247b7b;
$blue-two: #44a1a0;
$blue-three: #d0f6fa;
$blue-four: rgb(235, 240, 246);

$modal-background: rgba(0, 0, 0, 0.85);
// @import 'styles.scss';
$input-padding: 0.4rem 0.8rem;
$mobile-width: 720px;
$mobile-query: 720px;

//filters
$dark-green-svg: invert(29%) sepia(37%) saturate(912%) hue-rotate(136deg)
  brightness(92%) contrast(95%);
$white-svg: invert(100%) sepia(13%) saturate(7359%) hue-rotate(292deg)
  brightness(121%) contrast(101%);
$red-svg: invert(49%) sepia(85%) saturate(609%) hue-rotate(308deg)
  brightness(105%) contrast(101%);

@keyframes fade-in-up-animation {
  0% {
    opacity: 0.2;
    transform: translateY(10%);
  }
  100% {
    opacity: 1;
    transform: translateY(0%);
  }
}
