@import "../../../../../styles.scss";

.patient-form-modal {
  top: 0;
  position: absolute;
  z-index: 11;
  background-color: $modal-background;
  height: inherit;
  width: 100%;
  display: flex;
  align-items: center;
  visibility: visible;
}

// para situar la imagen
.patient-form-container {
  display: flex;
  width: 90%;
  max-width: 800px;
  margin: auto;
  border-radius: 1rem;
  background-color: $white;
  animation: 0.2s fade-in-up-animation 0s ease-in-out alternate;

  .patient-form-header {
    display: flex;
    justify-content: space-between;
    padding: 0.5rem 0;
    img {
      height: 28px;
      filter: $red-svg;
      cursor: pointer;
      transition: transform 0.2s ease;
      &:hover {
        transform: scale(0.9);
      }
    }
  }

  .patient-form-image {
    max-height: 100%;
    width: 50%;
    max-width: 300px;
    object-fit: cover;
    display: none;
  }

  // Formulario de registro
  .patient-form {
    position: relative;
    width: 100%;
    padding: 2rem;
    margin: auto;
    // display: grid;
    // gap: 1rem;
    // grid-template-columns: 1fr;
    // grid-template-rows: auto;
    border-radius: 1rem;
    .btn-showform {
      padding: $input-padding;
      background-color: $blue-one;
      color: $white;
      outline: none;
      border: none;
      border-radius: $input-padding;
      margin: 1rem 0;
    }
    .patient-content-box {
      max-height: 300px;
      overflow-y: auto;
      padding: 0.5rem 0;
      &::-webkit-scrollbar {
        width: 1px;
      }
    }

    .patient-form-icon {
      position: absolute;
      display: flex;
      justify-content: center;
      top: 0;
      right: 50%;
      height: 60px;
      width: 60px;
      border-radius: 50%;
      background-color: $blue-one;
      transform: translate(50%, -50%);
      padding: 0.8rem;
      img {
        filter: $white-svg;
        height: 100%;
      }
    }
  }
}
@media (min-width: $mobile-width) {
  .patient-form-modal {
    height: inherit;
    width: 100%;
    .patient-form-image {
      display: inline-block;
    }
