@import "../styles.scss";

.btn-action {
  padding: 0.8rem 1rem;
  width: 100%;
  border: none;
  background-color: $blue-one;
  color: white;
  border-radius: $input-padding;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 1px;
  cursor: pointer;
  transition: background 0.1s ease;

  &:hover {
    background-color: $blue-two;
  }

  &:disabled{
    opacity: .5;
  }
}
