.login-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  width: 100%;
  padding: 1rem 2rem;
  margin: auto;
  border-radius: 20px;
}

// CONTENEDOR DE INPUT
.login-input-div {
  display: flex;
  flex-direction: column;
  width: 100%;
}

// BOTONES
.login-btn-div {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 50px;

  & div {
    transform: scale(0.5) translateY(-60%);
  }
}
