@import "../styles.scss";

.pagination-div {
  position: sticky;
  z-index: 9;
  background-color: rgba(255, 255, 255, 0.508);
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem 0;
  gap: 0.5rem;
  width: 100%;
  .search-input {
    padding: 0.8rem 0.8rem;
    border-radius: 1rem;
    border: none;
    outline: none;
    background-color: $blue-three;
    &::placeholder {
      font-weight: bold;
      text-align: center;
    }
    &:focus {
      background-color: $blue-four;
    }
  }
  .pagination-numbers {
    border-radius: 1rem;
    overflow: hidden;
  }
  .pagination-nav {
    height: 40px;
    width: 40px;
    border: none;
    outline: none;
    color: $white;
    background-color: $dark-green;
    border-radius: 0.4rem;
    display: inline-block;
    cursor: pointer;
    transition: opacity 0.2s ease, background-color 0.2s ease,
      transform 0.2s ease;
    &:disabled {
      opacity: 0.2;
      cursor: default;
    }
    &:hover {
      background-color: $blue-two;
    }
    &:active {
      transform: scale(0.9);
    }

    img {
      height: 25px;
      filter: $white-svg;
    }
  }

  .page-item {
    padding: 1rem;
    border-radius: 0;
    border: none;
    background-color: $blue-three;
    font-weight: bold;
    color: $font-color;
    font-size: 1rem;
    cursor: pointer;
    transition: background 0.2s ease;
    &:disabled {
      opacity: 0.2;
      cursor: default;
    }
    &:hover,
    &.active {
      background-color: $blue-one;
      color: $white;
    }
  }
}
