@import "../../styles.scss";

.grid-container {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 60px calc(100vh - 120px) 60px;
  grid-template-areas:
    "header header"
    "container container"
    "navbar navbar";
  overflow: hidden;
}

.app-container {
  position: relative;
  height: calc(100vh - 120px);
  grid-area: container;
  background-color: rgba(255, 255, 255, 0.507);
  animation: 0.8s container-animation 0s ease-in-out alternate;
}

.app-container-content {
  height: inherit;
  display: flex;
  justify-content: center;
  align-items: center;
  h2 {
    
    animation: 0.2s fade-in-up-animation 0s ease-in-out alternate;
  }
}

@media (min-width: 720px) {
  .grid-container {
    grid-template-columns: 80px 1fr;
    grid-template-rows: 60px 1fr;
    grid-template-areas:
      "header header"
      "navbar container"
      "navbar container";
  }
  .app-container {
    height: calc(100vh - 60px);
    width: 100%;
    margin-left: auto;
  }
}

@keyframes container-animation {
  0% {
    transform: translateX(100%);
    opacity: 0;
  }
  100% {
    transform: translateX(0%);
    opacity: 1;
  }
}
