@import "../../../styles.scss";

.logout-btn {
  outline: none;
  border: 1px solid $dark-green;
  display: flex;
  align-items: center;
  width: auto;
  border-radius: 1rem;
  gap: 0.5rem;
  padding: $input-padding;
  cursor: pointer;
  transition: transform 0.2s ease, background-color 0.2s ease;
  transform: scale(0.9);

  &:hover {
    background-color: $dark-green;
    transform: scale(1);

    span {
      color: $white;
    }
    img {
    transition: filter 0.2s ease;
      filter: $white-svg;
    }
  }
  span {
    font-weight: 800;
    color: $dark-green;
    transition: color 0.2s ease;
  }
  img {
    filter: $dark-green-svg;
    height: 28px;
    width: 28px;
  }
}
