.lds-dual-ring {
  display: inline-block;
  height: 80px;
}
.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 45px;
  height: 45px;
  margin: 0px;
  border-radius: 50%;
  border: 8px solid #fff;
  border-color: #fff transparent #fff transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}
@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}