@import '../../../styles.scss';

.login-input{
    padding: .8rem 1rem;
    width: 100%;
    margin: auto;
    border: thin solid $blue-two;
    border-radius: .8rem;
    margin-bottom: .5rem;
    outline: none;

    &:focus{
    border: thin solid $orange;
    }
    &::placeholder{
        color: $blue-one;
        font-weight: bold;
        letter-spacing: 1px;
        font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
        "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue";
    }
    &.form-error{
        border: thin solid $light-red;
    }
}
