@import "../styles.scss";

.confirm-message-modal {

  background-color: $modal-background;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: calc(100vh - 60px);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 11;
//   visibility: hidden;
//   opacity: 0;
  &.visible{
      visibility: visible;
      opacity: 1;
  }
  .confirm-message {
    width: 90%;
    max-width: 400px;
    padding: 1rem;
    border-radius: 1rem;
    animation: 0.2s fade-in-up-animation 0s ease-in-out alternate;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    background-color: $white;
    min-height: 200px;

    .confirm-message-title {
      font-weight: bold;
      font-size: 1.2rem;
    }
    .confirm-message-body {
      font-size: 1.2rem;
    }
  }
  .confirm-action-btn {
    display: flex;
    gap: 1rem;
    button {
      padding: 0.6rem;
      width: 60px;
      border: none;
      outline: none;
      background-color: $dark-green;
      color: $white;
      border-radius: 1rem;
      cursor: pointer;
      &:hover {
        background-color: $blue-two;
      }
    }
  }
}
