@import "../styles.scss";
.form-input-box {
  label {
    display: inline;
  }
  .form-input {
    padding: 0.8rem 1rem;
    width: 100%;
    min-width: 10px;
    margin: auto;
    border: none;
    border-radius: 0.8rem;
    margin-bottom: 0.5rem;
    outline: none;
    background-color: $blue-four;
    border-bottom: 2px solid $blue-two;
    &:focus {
      background-color: $white;
      border-bottom: 2px solid $orange;
    }
    &::placeholder {
      font-size: 0.8rem;
      color: $blue-one;
      font-weight: bold;
      letter-spacing: 1px;
      font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue";
    }
    &.form-error {
      border-bottom: 2px solid $light-red;
    }
  }
}
