@import "../../../../../styles.scss";

.users-grid-container{
    height: calc(100vh - 125px);    
    overflow-y: auto;
    &::-webkit-scrollbar {
        display: none;
    }

}
.users-grid{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(10rem,1fr));
    grid-template-rows: auto;
    gap: .5rem;
    justify-items: center;
    padding: 1rem;
   
}

@media (min-width: $mobile-query){
    .users-grid-container{
        height: inherit;
        padding-bottom: 2rem;
    }
}