@import "../../styles.scss";

.login-h2 {
  text-align: center;
}
.login-page-container {
  min-height: 100vh;
  padding: 1rem;

  padding-bottom: 3rem;
  display: flex;
  align-items: center;
  max-width: 800px;
  margin: auto;
}

.login-form-container {
  background-color: $blue-four;
  width: 100%;
  margin: auto;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  border-radius: 2rem;
  box-shadow: 0px 2px 5px 2px rgba(79, 134, 143, 0.75);
  -webkit-box-shadow: 0px 2px 5px 2px rgba(79, 134, 143, 0.75);
  -moz-box-shadow: 0px 2px 5px 2px rgba(79, 134, 143, 0.75);
}

.login-image {
  height: 200px;
  object-fit: cover;
  max-width: auto;
  border-radius: 2rem;
}

.forget-password {
  text-decoration: none;
  text-align: center;
  display: block;
  color: $blue-one;
}

@media (min-width: $mobile-width) {
  .login-page-container {
    padding-bottom: initial;
    padding: 1rem;
  }
  .login-form-container {
    flex-direction: row;
  }
  .login-image {
    height: 100%;
    max-width: 400px;
  }
}
